@charset "UTF-8";
.home {
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.page-template {
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.page-template-default {
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.article:hover, .three-article:hover, .five-articles:hover, .four-article:hover {
  background: #e2e8ff;
}

.button-secondary {
  background-color: #00427f;
  color: #ffffff;
  padding: 10px;
  border: none;
  box-shadow: none;
  cursor: pointer;
}

.stripe-checkout-button {
  background-color: #00427f;
  color: #ffffff;
  padding: 10px;
  border: none;
  box-shadow: none;
  cursor: pointer;
}

.stripe-checkout-button:hover {
  background-color: #011e39;
  color: #ffffff;
  padding: 10px;
  border: none;
  box-shadow: none;
  cursor: pointer;
}

a {
  text-decoration: none;
}

.top-header {
  width: 100%;
  background: #00427F;
  padding: 10px;
  box-sizing: border-box;
}

.top-header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.left-content {
  color: white;
  font-weight: 900;
  font-family: "Roboto Slab", sans-serif;
  font-size: 10px;
  line-height: 13.19px;
}

.right-content {
  color: white;
  font-weight: 900;
  font-family: "Roboto Slab", sans-serif;
  font-size: 10px;
  line-height: 13.19px;
  margin-left: auto;
}

.login-button {
  width: 43px;
  height: 35px;
  background: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.login-icon {
  width: 100%;
  height: 100%;
  fill: white;
}

.next-row {
  display: flex;
  border-bottom: 0.5px solid #000000;
}

.logo-column {
  height: 95px;
  margin-top: -5px;
  width: 55%;
}

.aktuella-matcher {
  width: 20%;
  background-color: #F0F1F5;
  margin-top: 10px;
  margin-bottom: 10px;
  height: 95px;
}

.small-title {
  font-family: Roboto Slab, sans-serif;
  font-size: 18px;
  font-weight: 800;
  text-align: left;
  text-transform: uppercase;
  padding: 5px;
  margin: 0;
}

.match-column-one {
  width: 50%;
  text-transform: uppercase;
  float: left;
}

.match-column-two {
  width: 50%;
  text-transform: uppercase;
  float: right;
}

.match-title {
  font-family: Roboto Slab;
  font-size: 12px;
  font-weight: 500;
  line-height: 5px;
  letter-spacing: -0.01em;
  text-align: left;
  padding-left: 5px;
}

.featured-column {
  width: 25%;
  height: 95px;
}

.author-photo {
  float: left;
  margin-right: 0;
  margin-left: 10px;
  margin-top: 10px;
  height: 95px;
  width: auto;
}

.column-title {
  text-align: center;
  font-family: Roboto Slab;
  font-size: 16px;
  font-weight: 600;
  line-height: 18px;
  letter-spacing: -0.01em;
  text-align: center;
  text-decoration: none !important;
}

.author-title {
  font-family: Barlow Condensed;
  font-size: 20px;
  font-weight: 700;
  line-height: 0;
  text-align: center;
  margin-top: 0px;
}

.menu-row {
  display: flex;
  justify-content: space-between;
  padding-top: 5px;
}

.menu-column {
  width: 75%;
  display: flex;
  align-items: center;
}
.menu-column ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}
.menu-column ul li {
  font-family: "Poppins", sans-serif;
  font-weight: 800;
  font-size: 18px;
  line-height: 27px;
  text-align: justify;
  color: #00427F;
  transition: transform 0.3s ease;
}
.menu-column ul li:hover {
  color: #01182e;
}
.menu-column ul li a {
  text-decoration: none;
  color: inherit;
}
.menu-column ul li:not(:last-child) {
  margin-right: 10px;
}
.menu-column ul li:not(:last-child):after {
  content: "|";
  margin-left: 10px;
}

.column {
  padding: 10px;
  box-sizing: border-box;
}

.social-icons-column {
  width: 10%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.social-icons-column .social-icon {
  width: 28px;
  height: 28px;
  background-size: cover;
  cursor: pointer;
}
.social-icons-column .social-icon.facebook {
  background-image: url("/wp-content/themes/bollsvenskan/assets/images/facebook.svg");
}
.social-icons-column .social-icon.twitter {
  background-image: url("/wp-content/themes/bollsvenskan/assets/images/twitter.svg");
}
.social-icons-column .social-icon.whatsapp {
  background-image: url("/wp-content/themes/bollsvenskan/assets/images/whatsapp.svg");
}
.social-icons-column .social-icon.plus {
  background-image: url("/wp-content/themes/bollsvenskan/assets/images/plus.svg");
  height: 30px;
}

.language-selector-column {
  width: 10%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-family: "Poppins", sans-serif;
  font-weight: 800;
  font-size: 18px;
  line-height: 27px;
  text-align: justify;
  color: #00427F;
  margin-right: 2%;
}
.language-selector-column a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

.teams-filter-row {
  display: flex;
  justify-content: space-between;
  align-items: center; /* Align items vertically in the center */
  padding-left: 2px;
  box-sizing: border-box;
  margin-top: 10px;
}

.teams-filter-column {
  width: 75%;
  display: flex;
  align-items: center;
}
.teams-filter-column p {
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  letter-spacing: -0.01em;
  text-align: right;
  color: #000000;
  text-transform: uppercase;
  margin-right: 5px;
}
.teams-filter-column .team-logos {
  display: flex;
  align-items: center;
  gap: 5px;
  cursor: pointer;
}
.teams-filter-column .team-logo {
  width: 28px;
  height: 28px;
  transition: transform 0.3s ease;
}
.teams-filter-column .team-logo:hover {
  transform: scale(1.2);
}

.date-column {
  width: 20%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  letter-spacing: -0.01em;
  text-align: right;
  color: #000000;
  text-transform: uppercase;
  margin-right: 25px;
}

.footer-row {
  background-image: url(http://bollsvenskan.local/wp-content/uploads/2024/04/Rectangle-44.jpg);
  background-size: cover;
  height: 110px;
  margin: 0;
}

.footer-sm-row {
  display: block;
  align-items: center;
  height: 35px;
  margin-top: 70px;
}

.social-media-column {
  flex: 1;
}
.social-media-column .social-media-links {
  display: flex;
}
.social-media-column .social-media-links a {
  display: flex;
  align-items: center;
  text-decoration: none;
  margin-right: 20px;
  color: #000;
  font-family: Roboto Slab, sans-serif;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: -0.01em;
  transition: transform 0.3s ease;
}
.social-media-column .social-media-links a:hover {
  color: #00427f;
  transform: scale(1.1);
}
.social-media-column .social-media-links a img {
  width: 26px;
  height: 26px;
  margin-right: 0px;
}

.footer-row {
  display: flex;
}

.footer-column {
  width: 10%;
  display: flex;
  flex-direction: column;
  padding-left: 10px;
}
.footer-column:nth-child(2) {
  width: 15%;
}
.footer-column:nth-child(3) {
  width: 10%;
}
.footer-column h4 {
  color: #ffffff;
  font-family: "Roboto Slab";
  font-size: 12px;
  font-weight: 900;
  line-height: 15.83px;
  letter-spacing: -0.01em;
  text-align: left;
  margin: 0;
  margin: 10px 0 10px;
}
.footer-column p {
  color: #ffffff;
  font-family: "Roboto Slab";
  font-size: 11px;
  font-weight: 400;
  line-height: 14.51px;
  letter-spacing: -0.01em;
  text-align: left;
  margin: 0;
}
.footer-column a {
  color: #000000;
  font-family: "Roboto Slab", sans-serif;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: -0.01em;
  line-height: 16px;
}
.footer-column a:hover {
  color: #00427f;
}
.footer-column .accordian__item-content {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.footer-separator {
  border: 1px solid #00427f;
  margin: 20px 0;
}

.copyright-column {
  width: 25%;
  display: inline-block;
  vertical-align: top;
}
.copyright-column p {
  font-size: 14px;
  line-height: 1.6;
  color: #000000;
}

/* Add the following styles for the menu column */
.footer-menu-column {
  width: 75%;
  display: block;
  align-items: right;
}
.footer-menu-column .footer-menu {
  display: block;
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: right;
}
.footer-menu-column .footer-menu li {
  color: #00427f;
  font-family: Poppins, sans-serif;
  font-size: 18px;
  font-weight: 800;
  line-height: 27px;
  margin-right: 10px;
  text-align: justify;
  list-style: none;
  display: inline;
}
.footer-menu-column .footer-menu li a {
  text-decoration: none;
  color: inherit;
  transition: transform 0.3s ease;
}
.footer-menu-column .footer-menu li a:hover {
  transform: scale(1.1);
}

