/* acf-article-grid.scss */
.article-grid-module {
  display: -ms-grid;
  display: grid;
  grid-template-columns: 2fr 0fr;
  width: 75%;
  margin-top: 10px;
  margin-bottom: 10px;
}

.article-grid {
  grid-template-columns: 2fr 1fr;
  display: grid;
  gap: 10px;
}

.article {
  margin-bottom: 10px;
  background: #F0F1F5;
  padding: 10px;
}

.article-image img {
  width: 100%;
  object-fit: cover;
  max-height: 190px;
  margin: 0;
}

.article-grid-bottom .article-image img {
  min-height: 100px;
  margin: 0;
}

.article-content {
  text-align: left;
  color: #000;
}

.article-big-title {
  color: #000;
  font-family: Barlow Condensed, sans-serif;
  font-size: 25px;
  font-weight: 700;
  letter-spacing: -0.01em;
  line-height: 30px;
  margin-top: 2px;
  margin-bottom: 5px;
}

.article-big-title:hover {
  color: #00427f;
}

.article-small-title {
  font-family: Barlow Condensed;
  font-size: 15px;
  font-weight: 700;
  line-height: 18px;
  letter-spacing: -0.01em;
  text-align: left;
  margin-top: 2px;
  margin-bottom: 5px;
}

.article-small-title:hover {
  color: #00427f;
}

.article-small-bottom-title {
  font-family: Barlow Condensed;
  font-size: 15px;
  font-weight: 700;
  line-height: 18px;
  letter-spacing: -0.01em;
  text-align: left;
  margin-top: 2px;
  margin-bottom: 5px;
}

.article-small-bottom-title:hover {
  color: #00427f;
}

.article-content .article-excerpt {
  font-family: "Roboto Slab", sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  letter-spacing: -0.01em;
  text-align: justify;
  color: #000;
  margin-top: 5px;
}

.article-grid-bottom {
  column-count: 2;
  gap: 10px;
}

.article-image-big img {
  width: 100%;
  height: 426px;
  object-fit: cover;
  max-height: 426px;
  margin: 0;
}

.three-articles-module {
  width: 75%;
  display: grid;
  grid-template-columns: 3fr;
  gap: 10px;
}

.three-articles-grid {
  display: flex;
  margin-top: 0px;
  width: 100%;
}

.three-article {
  width: 100%;
  margin: 5px;
  padding: 10px;
  background: #f0f1f5;
}
.three-article .three-article-link {
  text-decoration: none;
}
.three-article .three-article-link .three-article-image img {
  width: 100%;
  height: 180px;
  margin-bottom: 0;
  margin: 0;
  max-height: 180px;
}
.three-article .three-article-link .three-article-content {
  padding: 10px;
  text-align: left;
  color: #000;
}
.three-article .three-article-link .three-article-content .three-article-title {
  font-family: Barlow Condensed;
  font-size: 15px;
  font-weight: 700;
  line-height: 18px;
  letter-spacing: -0.01em;
  text-align: left;
  margin: 0;
}
.three-article .three-article-link .three-article-content .three-article-title:hover {
  color: #00427f;
}
.three-article .three-article-link .three-article-content .three-article-excerpt {
  color: #000;
  font-family: Roboto Slab, sans-serif;
  font-size: 12px;
  font-weight: 500;
  letter-spacing: -0.01em;
  line-height: 16px;
  margin-top: 5px;
  text-align: justify;
}

.four-articles-module {
  display: -ms-grid;
  display: grid;
  gap: 10px;
  grid-template-columns: 4fr;
  width: 100%;
}

.four-articles-grid {
  display: flex;
  margin-top: 0px;
  width: 100%;
}

.four-article {
  width: 100%;
  margin-bottom: 0px;
  padding: 10px;
  margin-right: 5px;
  margin-left: 5px;
  background: #f0f1f5;
}

.four-article-link {
  text-decoration: none;
}

.four-article-image img {
  width: 100%;
  height: auto;
  margin-bottom: 0;
  margin: 0;
  max-height: 180px;
}

.four-article-content {
  padding: 10px;
  text-align: left;
  color: #000;
}

.four-article-title {
  font-family: Barlow Condensed;
  font-size: 15px;
  font-weight: 700;
  line-height: 18px;
  letter-spacing: -0.01em;
  text-align: left;
  margin: 0;
}

.four-article-title:hover {
  color: #00427f;
}

.four-article-excerpt {
  color: #000;
  font-family: Roboto Slab, sans-serif;
  font-size: 12px;
  font-weight: 500;
  letter-spacing: -0.01em;
  line-height: 16px;
  margin-top: 5px;
  text-align: justify;
}

.separator {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

hr {
  margin: 20px 0;
}

img {
  max-width: 100%;
  height: auto;
  margin: 20px 0;
}

